  :root {
    --montserrat: 'Montserrat', sans-serif;
    --openSans: 'Open Sans', sans-serif;
    --roboto: 'Roboto', sans-serif;
  }

  * {
    font-family: var(--openSans);
  }

  .arrowBack {
    position: fixed;
    right:0;
    padding: 20px;
    margin-right:30px;
    color:white;
    z-index:9999!important;
    transition: 0.3s ease;
    cursor: pointer;
    top:-100px;
    font-size:1.7em;
  }
  html {
    scroll-snap-type: mandatory;
    scroll-snap-points-y: repeat(100vh);
    scroll-snap-type: y mandatory;

  }
  section{
    scroll-snap-align: start;
    height:100vh;
  }

.section-about-1 {
  /*
  background: rgb(75,189,240);
  background: linear-gradient(180deg, rgba(75,189,240,1) 0%, rgba(0,255,128,1) 100%); 
  */

  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;

  display:flex;
  justify-content: center;
  align-items: center;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.section-about-1 > h2 {
  font-weight:bold;
  font-size:7em;
  color:white;
}

.section-about-2 {
  background:#212121;
}
.section-about-3 {
  background: rgb(33,33,33);
  background: linear-gradient(180deg, rgba(33,33,33,1) 0%, rgba(0,56,119,1) 100%); 
}

.row-section{
  height:100%;
}

.col-section{
  display:flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width:576px) {
.section-about-1 > h2 {
  font-size:5em;
}
}

/* SECTION INFORMATION GENERAL */

.col-information {
  text-align: center;
  padding:50px;
}

.col-information > p {
  color: white;
  letter-spacing: 3px;
  font-size:1.4em;
  margin-bottom:0px;
}

.col-information > h3 {
color: #1bffba;
font-weight: 700;
font-size:4.5em;
}

.col-information > hr {
  margin-bottom:5px;
}

.col-information > h5 {
  color:white;
  font-size:1.2em;
  letter-spacing: 3px;
}

.col-information-main-text > span {
  color: #afafaf;
  padding:20px;
  font-size:1.1em;
}

.col-goDown{
  height:100%;
  display:flex;
  justify-content: center;
  align-items: center;
}

.col-goDown > a {
  font-size:4em;
  color: #1bffba;
  width:1.7em;
  height:1.7em;
  background-color:none;
  border-radius: 50%;
  border: 7px solid #1bffba;
  text-align:center;
}

.col-goDown > a:hover {
  color:#bcffeb;
  border: 7px solid #bcffeb;
}

#row-aboutMe{
  height:100%;
}

@media screen and (max-width:576px) {

  #row-aboutMe{
    display:block;
  }
  
  .col-information {
    padding-bottom:0px;
  }
  .col-information > h3 {
    font-size:3.5em;
    }

    .col-information-main-text {
      padding-top:20px;
    }

  .col-information-main-text > span {
    display:flex;
    justify-content: center;
    font-size:0.9em;
    padding:0;
  }

  .col-goDown > a {
    font-size:3em;
  }
}

/* SECTION PROFILE */

