/* BUTTONS STUDIES WORK */

.infoWork {
  display:none;
}

.infoStudies {
  display:block;
}

.buttons {
box-shadow: none;
background: none;
color: #67b1b3;
width:50%;
border:none;
border-bottom:1px solid transparent;
transition:0.1s ease-in-out;
height:35px;
}

.studies-default {
  background-color:rgba( 83, 227, 236 , 0.2);
  color: #2cf0f5;
  border-bottom: 1px solid #2cf0f5;
}

.buttons:hover {
  background-color:rgba( 83, 227, 236 , 0.2);
  color: #2cf0f5;
  border-bottom: 1px solid #2cf0f5;
}

.buttons:active {
  background-color:rgba( 83, 227, 236 , 0.2);
  color: #2cf0f5;
  border-bottom: 1px solid #2cf0f5;
}

.buttons:focus {
  background-color:rgba( 83, 227, 236 , 0.2);
  color: #2cf0f5;
  border-bottom: 1px solid #2cf0f5;
}

.ul-info{
  padding-left:0;
}

.listInfo{
  list-style: none;
  float: left;
  color: white;
  font-size:0.8em;
  line-height: 2;
}

.listInfo::before{
  content: "\2022";
  color:#2cf0f5;
  width: 1em; 
  margin-left: -1em;
  font-weight: bold;
  display: inline-block;
}

.section-about-4 {
  height:100vh;
  background: rgba(0,56,119,1); 
  display:flex;
  justify-content: space-around;
  align-items: center;

}

/* CARDS  */
.list-columns {
  margin-top:20px;
}
.list-projects {
  display:flex;
  justify-content: center;
  align-items: center;
  height:100%;
}
.list-projects > .card {
border-radius: 0px;
border:0px;
transition: 0.2s;
cursor:pointer;
max-width:100%;
min-width:90%;
height:100%;
}

.card > img {
  object-fit: cover;
  height:300px;
  border-radius: 0px;
  width:100%;
}

.list-projects > .card:hover {
  -webkit-box-shadow: 0px 0px 15px 0px rgba(28,28,28,0.86);
-moz-box-shadow: 0px 0px 15px 0px rgba(28,28,28,0.86);
box-shadow: 0px 0px 15px 0px rgba(28,28,28,0.86);
}

.list-projects > .card:hover .card-footer {
  background-position: -100% 100%;
  color:black;
}

.list-projects > .card:hover a {
  background-position: -100% 100%;
  color:black;
}

.list-projects > .card:hover .card-footer {
  background-position: -100% 100%;
  color:black;
}


.list-projects > .card > .card-footer {
  color: white;
  border:none;
  border-radius: 0px;
  background-image: linear-gradient(90deg,
  #272838 0%,
  #272838 50%,
  #FFF 0%,
  #FFF 100%);
background-size: 200%;
transition: background-position .3s cubic-bezier(.47, .1, 1, .63),
color .2s linear;
height:100%;
}

.card-footer > .card-title {
  font-weight: 700;
  font-size: 1.5em;
  margin-bottom:2%;
}

.card-footer > .card-title > a {
  text-decoration: none;
  color: white;
  transition: 0.3s;
}

.card-footer > .card-text {
  letter-spacing: 0.5px;
  font-size:0.9em;
  color: #b6b6b6;
}

.projects-section {
margin-bottom:5px;
text-transform: uppercase;
letter-spacing: 2px;
font-size:0.8em;
color: grey;
}

@media screen and (max-width:576px) {
  .list-projects{
    display:block;
  }
.section-about-4 > .row {
margin:0;
}

.card > .card-img-top {
  height:150px;
}
}