  header {
    position: absolute;
    top:0;
    width:100%;
    z-index:9999 !important;
    transition: top 0.5s;
  }

  header > ul {
    padding:0;
    display:flex;
    justify-content: center;
    align-items: center;
  }
  
header > ul > li {
  display:inline-flex;
  width:120px;
  height:60px;
  justify-content: center;
  align-items: center;
  border-top:4px solid transparent;
  transition: .2s ease;
  font-size:1.1em;
}

header > ul > li:hover {
  border-top:4px solid white;
}

.header-buttons > a{
  text-decoration: none !important;
  color: white;
  font-family: var(--roboto);
  width:100%;
  height:100%;
  display:flex;
  align-items:center;
  justify-content: center;
  transition:0.1s;
}

.header-buttons > a:hover{
  color:white;
  transform: scale(1.2);
}